@import url("//fonts.googleapis.com/css?family=Eczar:800|Open+Sans:600&subset=cyrillic");
body {
  background: fixed #ffd6d6 url("http://prostopic.net/images/2018/02/ah8oi4js1ypj546193ox.png") center no-repeat;
  background-size: cover;
}

.container {
  height: 100vh;
  padding-top: 5vh;
  padding-bottom: 5vh;
  background-color: rgba(0, 153, 59, 0.45);
}

.title {
  color: #fffcfc;
}

.title__text {
  vertical-align: middle;
  font-family: 'Eczar', serif;
  font-size: 6rem;
}

.title__text i.fas {
  font-size: 4rem;
}

.title__description {
  font-family: 'Open Sans', sans-serif;
  font-size: 0.7rem;
  padding-left: 3.5rem;
}

.main {
  height: 50vh;
}

.main .sqr {
  text-decoration: none;
  background-color: #fffcfc;
  border: #00993b solid 2px;
  border-radius: 0 40px 0 40px;
  padding: 15px 20px;
}

.main .sqr:hover {
  background-color: #00993b;
  border: #aaa solid 2px;
}

.main .sqr:hover .sqr__icon {
  color: #aaa;
}

.main .sqr:hover .sqr__description {
  color: #aaa;
}

.main .sqr__icon {
  font-size: 3.5rem;
  color: #00993b;
}

.main .sqr__description {
  margin-top: 10px;
  line-height: 1.5rem;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.7rem;
  color: #00993b;
}

footer h5,
footer a {
  font-family: 'Open Sans', sans-serif;
  font-size: 0.8rem;
  color: #fffcfc;
}

footer h5 i,
footer a i {
  padding-right: 5px;
}

footer .info {
  cursor: pointer;
}

footer .info:hover {
  color: #2e2e2e;
}

footer .info:hover .dashed {
  border-bottom: #2e2e2e dashed 1px;
}

footer .info .dashed {
  border-bottom: #fffcfc dashed 1px;
}

footer a,
footer a:hover {
  text-decoration: none;
}

footer a:hover {
  color: #2e2e2e;
}

.modal-content {
  font-family: 'Open Sans', sans-serif;
  color: #00993b;
}

.modal-content a,
.modal-content a:hover {
  text-decoration: none;
}